// src/pages/auth/EvaluationInvite.tsx
import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import {
  Container, Paper, Typography, Box, TextField, Button, Alert, CircularProgress,
  Card, CardContent, Chip, Stepper, Step, StepLabel, InputAdornment, IconButton,
  Divider, List, ListItem, ListItemIcon, ListItemText
} from '@mui/material';
import {
  Visibility, VisibilityOff, Assignment, Person, Business, CheckCircle, Warning,
  Lock, Email, Quiz, Error, AccountCircle, Work
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';
import api from '../../services/api';

interface InviteDetails {
  id_invite: string;
  token: string;
  id_entreprise: string;
  id_evaluation?: string;
  nom_entreprise: string;
  email: string;
  nom_prenom: string;
  fonction: string;
  role: string;
  statut: 'ATTENTE' | 'ACCEPTE' | 'EXPIRE';
  date_expiration: string;
  user_exists: boolean;
  actor_exists: boolean;
}

interface ValidationErrors {
  [key: string]: string;
}

const EvaluationInvite: React.FC = () => {
  const navigate = useNavigate();
  const { token } = useParams<{ token: string }>();
  const { login, isAuthenticated } = useAuth();

  const [step, setStep] = useState<'loading' | 'expired' | 'setPassword' | 'login' | 'redirect'>('loading');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [inviteDetails, setInviteDetails] = useState<InviteDetails | null>(null);
  const [validationErrors, setValidationErrors] = useState<ValidationErrors>({});
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [loginPassword, setLoginPassword] = useState('');
  const [showLoginPassword, setShowLoginPassword] = useState(false);

  const steps = [
    { label: 'Validation du lien', icon: <Assignment />, color: '#0B4E87' },
    { label: 'Authentification', icon: <Lock />, color: '#C55A57' },
    { label: 'Acc√®s √† l\'√©valuation', icon: <Quiz />, color: '#09C4B8' }
  ];

  const validateInvitation = async () => {
  if (!token) {
    setError('Token d\'invitation manquant');
    setStep('expired');
    return;
  }
  try {
    setLoading(true);
    setError('');
    console.log('üîç Validation invitation:', token);
    const response = await api.get(`/evaluation-invite/validate/${token}`);
    const inviteData: InviteDetails = response.data;
    console.log('‚úÖ Invitation valid√©e:', inviteData);
    setInviteDetails(inviteData);
    if (inviteData.statut === 'EXPIRE' || new Date(inviteData.date_expiration) < new Date()) {
      setStep('expired');
      return;
    }
    if (inviteData.statut === 'ACCEPTE') {
      setError('Cette invitation a d√©j√† √©t√© utilis√©e');
      setStep('expired');
      return;
    }
    setStep(inviteData.user_exists ? 'login' : 'setPassword');
  } catch (error: any) {
    console.error('‚ùå Erreur validation invitation:', error);
    if (error.response?.status === 404) {
      setError('Invitation introuvable ou invalide');
      setStep('expired');
    } else {
      setError(error.response?.data?.message || 'Erreur lors de la validation de l\'invitation');
      setStep('expired');
    }
  } finally {
    setLoading(false);
  }
};

 const handleCreatePassword = async () => {
    if (!inviteDetails) return;
    const newErrors: ValidationErrors = {};
    if (!password) {
      newErrors.password = 'Le mot de passe est obligatoire';
    } else if (!validatePassword(password)) {
      newErrors.password = 'Le mot de passe doit contenir au moins 8 caract√®res';
    }
    if (!confirmPassword) {
      newErrors.confirmPassword = 'Veuillez confirmer le mot de passe';
    } else if (password !== confirmPassword) {
      newErrors.confirmPassword = 'Les mots de passe ne correspondent pas';
    }
    setValidationErrors(newErrors);
    if (Object.keys(newErrors).length > 0) {
      return;
    }
    try {
      setLoading(true);
      setError('');
      await api.post(`/evaluation-invite/${inviteDetails.id_invite}/create-account`, {
        password
      });
      console.log('‚úÖ Compte cr√©√© avec succ√®s');
      await login(inviteDetails.email, password);
    } catch (error: any) {
      console.error('‚ùå Erreur cr√©ation compte:', error);
      setError(error.response?.data?.message || 'Erreur lors de la cr√©ation du compte');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    validateInvitation();
  }, [token]);

  useEffect(() => {
    if (isAuthenticated && inviteDetails && step !== 'redirect') {
      setStep('redirect');
      api.post(`/evaluation-invite/${inviteDetails.id_invite}/accept`)
        .then(() => {
          console.log('‚úÖ Invitation accept√©e');
        })
        .catch(err => {
          console.error('‚ö†Ô∏è Erreur acceptation invitation:', err);
        });
      setTimeout(() => {
        navigate(`/maturity-evaluation/${inviteDetails.id_entreprise}`, {
          state: {
            fromInvite: true,
            inviteId: inviteDetails.id_invite,
            evaluationId: inviteDetails.id_evaluation
          }
        });
      }, 2000);
    }
  }, [isAuthenticated, inviteDetails, step, navigate]);

  const validatePassword = (pwd: string): boolean => {
    return pwd.length >= 8;
  };

  const validatePasswordStrength = (pwd: string): {
    score: number;
    message: string;
    color: 'error' | 'warning' | 'success';
  } => {
    if (pwd.length < 8) {
      return { score: 0, message: 'Trop court', color: 'error' };
    }
    let score = 0;
    if (pwd.length >= 8) score++;
    if (pwd.length >= 12) score++;
    if (/[A-Z]/.test(pwd)) score++;
    if (/[a-z]/.test(pwd)) score++;
    if (/[0-9]/.test(pwd)) score++;
    if (/[^A-Za-z0-9]/.test(pwd)) score++;
    if (score <= 2) {
      return { score: 33, message: 'Faible', color: 'error' };
    } else if (score <= 4) {
      return { score: 66, message: 'Moyen', color: 'warning' };
    } else {
      return { score: 100, message: 'Fort', color: 'success' };
    }
  };

 

  const handleLogin = async () => {
    if (!inviteDetails) return;
    if (!loginPassword) {
      setValidationErrors({ password: 'Le mot de passe est requis' });
      return;
    }
    try {
      setLoading(true);
      setError('');
      setValidationErrors({});
      await login(inviteDetails.email, loginPassword);
    } catch (error: any) {
      console.error('‚ùå Erreur connexion:', error);
      setError('Email ou mot de passe incorrect');
    } finally {
      setLoading(false);
    }
  };

  const renderStepContent = () => {
    switch (step) {
      case 'loading':
        return (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <CircularProgress size={60} sx={{ mb: 3 }} />
            <Typography variant="h6" gutterBottom>
              Validation de votre invitation...
            </Typography>
            <Typography variant="body2" color="text.secondary">
              V√©rification de votre lien d'√©valuation
            </Typography>
          </Box>
        );
      case 'expired':
        return (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <Error sx={{ fontSize: 64, color: 'error.main', mb: 3 }} />
            <Typography variant="h5" gutterBottom color="error">
              Invitation invalide ou expir√©e
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 4 }}>
              {error || 'Cette invitation n\'est plus valide ou a expir√©.'}
            </Typography>
            <Button
              variant="contained"
              onClick={() => navigate('/auth/login')}
              size="large"
            >
              Retour √† la connexion
            </Button>
          </Box>
        );
      case 'setPassword':
        return (
          <Box sx={{ py: 2 }}>
            <Box sx={{ textAlign: 'center', mb: 4 }}>
              <Lock sx={{ fontSize: 48, color: steps[1].color, mb: 2 }} />
              <Typography variant="h5" gutterBottom sx={{ color: steps[1].color, fontWeight: 700 }}>
                Cr√©ez votre mot de passe
              </Typography>
              <Typography variant="body2" color="text.secondary">
                D√©finissez un mot de passe s√©curis√© pour acc√©der √† votre √©valuation
              </Typography>
            </Box>
            {inviteDetails && (
              <>
                <Card variant="outlined" sx={{ mb: 3, backgroundColor: 'rgba(11, 78, 135, 0.05)' }}>
                  <CardContent>
                    <Typography variant="h6" gutterBottom>
                      Informations de votre invitation
                    </Typography>
                    <List dense>
                      <ListItem>
                        <ListItemIcon><Business color="primary" /></ListItemIcon>
                        <ListItemText primary="Entreprise" secondary={inviteDetails.nom_entreprise} />
                      </ListItem>
                      <ListItem>
                        <ListItemIcon><AccountCircle color="primary" /></ListItemIcon>
                        <ListItemText primary="Nom" secondary={inviteDetails.nom_prenom} />
                      </ListItem>
                      <ListItem>
                        <ListItemIcon><Email color="primary" /></ListItemIcon>
                        <ListItemText primary="Email" secondary={inviteDetails.email} />
                      </ListItem>
                      <ListItem>
                        <ListItemIcon><Work color="primary" /></ListItemIcon>
                        <ListItemText primary="Fonction" secondary={inviteDetails.fonction} />
                      </ListItem>
                    </List>
                    <Box sx={{ mt: 2, display: 'flex', gap: 1 }}>
                      <Chip size="small" label={inviteDetails.role} color={inviteDetails.role === 'Manager' ? 'primary' : 'secondary'} />
                      <Chip size="small" label="Premi√®re connexion" variant="outlined" />
                    </Box>
                  </CardContent>
                </Card>
                <Divider sx={{ my: 3 }} />
              </>
            )}
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
              <TextField
                required
                fullWidth
                type={showPassword ? 'text' : 'password'}
                label="Nouveau mot de passe"
                value={password}
                onChange={(e) => {
                  setPassword(e.target.value);
                  if (validationErrors.password) {
                    setValidationErrors(prev => {
                      const newErrors = { ...prev };
                      delete newErrors.password;
                      return newErrors;
                    });
                  }
                }}
                error={!!validationErrors.password}
                helperText={validationErrors.password || `S√©curit√©: ${validatePasswordStrength(password).message}`}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={() => setShowPassword(!showPassword)} edge="end">
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
              />
              <TextField
                required
                fullWidth
                type={showConfirmPassword ? 'text' : 'password'}
                label="Confirmer le mot de passe"
                value={confirmPassword}
                onChange={(e) => {
                  setConfirmPassword(e.target.value);
                  if (validationErrors.confirmPassword) {
                    setValidationErrors(prev => {
                      const newErrors = { ...prev };
                      delete newErrors.confirmPassword;
                      return newErrors;
                    });
                  }
                }}
                error={!!validationErrors.confirmPassword}
                helperText={validationErrors.confirmPassword}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={() => setShowConfirmPassword(!showConfirmPassword)} edge="end">
                        {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
              />
              <Button
                variant="contained"
                size="large"
                onClick={handleCreatePassword}
                disabled={loading || !password || !confirmPassword}
                startIcon={loading ? <CircularProgress size={20} /> : <Lock />}
                sx={{ py: 1.5, backgroundColor: steps[1].color }}
              >
                {loading ? 'Cr√©ation...' : 'Cr√©er mon compte et acc√©der'}
              </Button>
            </Box>
          </Box>
        );
      case 'login':
        return (
          <Box sx={{ py: 2 }}>
            <Box sx={{ textAlign: 'center', mb: 4 }}>
              <Person sx={{ fontSize: 48, color: steps[1].color, mb: 2 }} />
              <Typography variant="h5" gutterBottom sx={{ color: steps[1].color, fontWeight: 700 }}>
                Connexion √† votre compte
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Vous avez d√©j√† un compte, connectez-vous pour acc√©der √† l'√©valuation
              </Typography>
            </Box>
            {inviteDetails && (
              <>
                <Alert severity="info" sx={{ mb: 3 }}>
                  Un compte existe d√©j√† pour l'adresse <strong>{inviteDetails.email}</strong>.
                  Connectez-vous avec votre mot de passe pour acc√©der √† cette nouvelle √©valuation.
                </Alert>
                <Card variant="outlined" sx={{ mb: 3, backgroundColor: 'rgba(11, 78, 135, 0.05)' }}>
                  <CardContent>
                    <Typography variant="subtitle2" gutterBottom>
                      Invitation pour:
                    </Typography>
                    <Typography variant="body1" fontWeight="bold">
                      {inviteDetails.nom_entreprise}
                    </Typography>
                    <Box sx={{ mt: 1, display: 'flex', gap: 1 }}>
                      <Chip size="small" label={inviteDetails.fonction} variant="outlined" />
                      <Chip size="small" label={inviteDetails.role} color={inviteDetails.role === 'Manager' ? 'primary' : 'secondary'} />
                    </Box>
                  </CardContent>
                </Card>
              </>
            )}
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
              <TextField
                fullWidth
                label="Email"
                value={inviteDetails?.email || ''}
                disabled
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Email />
                    </InputAdornment>
                  )
                }}
              />
              <TextField
                required
                fullWidth
                type={showLoginPassword ? 'text' : 'password'}
                label="Mot de passe"
                value={loginPassword}
                onChange={(e) => {
                  setLoginPassword(e.target.value);
                  if (validationErrors.password) {
                    setValidationErrors({});
                  }
                }}
                error={!!validationErrors.password}
                helperText={validationErrors.password}
                onKeyPress={(e) => {
                  if (e.key === 'Enter') {
                    handleLogin();
                  }
                }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={() => setShowLoginPassword(!showLoginPassword)} edge="end">
                        {showLoginPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
              />
              {error && (
                <Alert severity="error" onClose={() => setError('')}>
                  {error}
                </Alert>
              )}
              <Button
                variant="contained"
                size="large"
                onClick={handleLogin}
                disabled={loading || !loginPassword}
                startIcon={loading ? <CircularProgress size={20} /> : <Person />}
                sx={{ py: 1.5, backgroundColor: steps[1].color }}
              >
                {loading ? 'Connexion...' : 'Se connecter et acc√©der'}
              </Button>
              <Box sx={{ textAlign: 'center' }}>
                <Button
                  variant="text"
                  onClick={() => navigate('/auth/forgot-password', { state: { email: inviteDetails?.email } })}
                  size="small"
                >
                  Mot de passe oubli√© ?
                </Button>
              </Box>
            </Box>
          </Box>
        );
      case 'redirect':
        return (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <CheckCircle color="success" sx={{ fontSize: 64, mb: 3 }} />
            <Typography variant="h5" gutterBottom color="success.main">
              Connexion r√©ussie !
            </Typography>
            <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
              Redirection vers votre √©valuation de maturit√©...
            </Typography>
            {inviteDetails && (
              <Box sx={{ mb: 3 }}>
                <Typography variant="body2" color="text.secondary">
                  Entreprise: <strong>{inviteDetails.nom_entreprise}</strong>
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  R√¥le: <strong>{inviteDetails.role}</strong>
                </Typography>
              </Box>
            )}
            <CircularProgress size={40} sx={{ color: steps[2].color }} />
          </Box>
        );
      default:
        return null;
    }
  };

  const getCurrentStep = () => {
    if (step === 'loading' || step === 'expired') return 0;
    if (step === 'setPassword' || step === 'login') return 1;
    if (step === 'redirect') return 2;
    return 0;
  };

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ overflow: 'hidden' }}>
        <Box sx={{
          background: `linear-gradient(135deg, #0B4E87 0%, #09C4B8 100%)`,
          color: 'white',
          p: 3,
          textAlign: 'center'
        }}>
          <Business sx={{ fontSize: 40, mb: 2 }} />
          <Typography variant="h4" gutterBottom fontWeight="bold">
            Invitation √† l'√©valuation DSIN
          </Typography>
          <Typography variant="h6" sx={{ opacity: 0.9 }}>
            Acc√©dez √† votre √©valuation de maturit√© personnalis√©e
          </Typography>
        </Box>
        {step !== 'loading' && step !== 'expired' && (
          <Box sx={{ p: 3, backgroundColor: 'grey.50' }}>
            <Stepper activeStep={getCurrentStep()} alternativeLabel>
              {steps.map((stepInfo, index) => (
                <Step key={stepInfo.label}>
                  <StepLabel
                    StepIconComponent={() => (
                      <Box
                        sx={{
                          width: 40,
                          height: 40,
                          borderRadius: '50%',
                          backgroundColor: index <= getCurrentStep() ? stepInfo.color : 'grey.300',
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          color: 'white'
                        }}
                      >
                        {stepInfo.icon}
                      </Box>
                    )}
                  >
                    <Typography
                      variant="body2"
                      sx={{
                        color: index <= getCurrentStep() ? stepInfo.color : 'text.secondary',
                        fontWeight: index === getCurrentStep() ? 'bold' : 'normal'
                      }}
                    >
                      {stepInfo.label}
                    </Typography>
                  </StepLabel>
                </Step>
              ))}
            </Stepper>
          </Box>
        )}
        <Box sx={{ p: 4, minHeight: 400 }}>
          {renderStepContent()}
        </Box>
        {inviteDetails && step !== 'redirect' && step !== 'expired' && (
          <Box sx={{
            p: 3,
            backgroundColor: 'grey.50',
            borderTop: '1px solid',
            borderColor: 'grey.200'
          }}>
            <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center' }}>
              Cette invitation expire le {new Date(inviteDetails.date_expiration).toLocaleDateString('fr-FR')}
            </Typography>
          </Box>
        )}
      </Paper>
    </Container>
  );
};

export default EvaluationInvite;