// src/pages/MaturityEvaluation.tsx
import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import {
  Container, Grid, Paper, Typography, Box, Button, LinearProgress, Card, CardContent,
  Radio, RadioGroup, FormControlLabel, FormControl, FormLabel, Tabs, Tab, Alert,
  CircularProgress, Chip, Divider, IconButton, Tooltip, Dialog, DialogTitle,
  DialogContent, DialogActions, Snackbar
} from '@mui/material';
import {
  Security, Computer, Storage, Code, Lightbulb, CheckCircle, ArrowForward, ArrowBack,
  Save, Assessment, Info, Timer, Refresh, CloudUpload, CloudDone, Error
} from '@mui/icons-material';
import api from '../services/api';
import { useAuth } from '../contexts/AuthContext';

interface Question {
  id_question: string;
  fonction: string;
  numero_question: number;
  texte_question: string;
  description?: string;
  poids: number;
  type_reponse: 'OUI_NON' | 'ECHELLE_1_5' | 'CHOIX_MULTIPLE';
  options_reponse?: string[];
  ordre_affichage: number;
}

interface QuestionsByFunction {
  [fonction: string]: Question[];
}

interface Response {
  id_question: string;
  valeur_reponse: string;
  score_question: number;
  commentaire?: string;
}

interface FunctionScore {
  fonction: string;
  score: number;
  niveau: string;
  progress: number;
}

interface EvaluationData {
  id_evaluation: string;
  id_entreprise: string;
  entreprise_nom: string;
  manager_id: string;
  date_debut: string;
  statut: 'BROUILLON' | 'EN_COURS' | 'TERMINE';
  responses: Record<string, Response>;
  scores: Record<string, FunctionScore>;
}

const functionsConfig = {
  cybersecurite: {
    label: 'Cybers√©curit√©',
    icon: <Security />,
    color: '#d32f2f',
    description: 'S√©curit√© des syst√®mes et donn√©es'
  },
  maturite_digitale: {
    label: 'Maturit√© Digitale',
    icon: <Computer />,
    color: '#1976d2',
    description: 'Transformation et strat√©gie digitale'
  },
  gouvernance_donnees: {
    label: 'Gouvernance des Donn√©es',
    icon: <Storage />,
    color: '#388e3c',
    description: 'Gestion et qualit√© des donn√©es'
  },
  devsecops: {
    label: 'DevSecOps',
    icon: <Code />,
    color: '#f57c00',
    description: 'D√©veloppement et op√©rations s√©curis√©es'
  },
  innovation_numerique: {
    label: 'Innovation Num√©rique',
    icon: <Lightbulb />,
    color: '#7b1fa2',
    description: 'Veille et adoption technologique'
  }
};

const scaleOptions = [
  { value: '1', label: '1 - Pas du tout', description: 'Aucune mise en place' },
  { value: '2', label: '2 - Partiellement', description: 'D√©but de mise en place' },
  { value: '3', label: '3 - Moyennement', description: 'Mise en place partielle' },
  { value: '4', label: '4 - Largement', description: 'Mise en place avanc√©e' },
  { value: '5', label: '5 - Totalement', description: 'Mise en place compl√®te' }
];

const useDebounce = (value: any, delay: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);
  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);
    return () => {
      clearTimeout(handler);
    };
  }, [value, delay]);
  return debouncedValue;
};

const MaturityEvaluation: React.FC = () => {
  const { enterpriseId } = useParams<{ enterpriseId: string }>();
  const navigate = useNavigate();
  const location = useLocation();
  const { currentUser } = useAuth();
  const [evaluationData, setEvaluationData] = useState<EvaluationData | null>(null);
  const [questionsByFunction, setQuestionsByFunction] = useState<QuestionsByFunction>({});
  const [currentFunction, setCurrentFunction] = useState(0);
  const [responses, setResponses] = useState<Record<string, Response>>({});
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState('');
  const [showConfirmDialog, setShowConfirmDialog] = useState(false);
  const [startTime] = useState(Date.now());
  const [lastSaveTime, setLastSaveTime] = useState<Date | null>(null);
  const [autoSaveStatus, setAutoSaveStatus] = useState<'idle' | 'saving' | 'saved' | 'error'>('idle');
  const [unsavedChanges, setUnsavedChanges] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarOpen, setSnackbarOpen] = useState(false);

  const functionKeys = Object.keys(functionsConfig);
  const debouncedResponses = useDebounce(responses, 5000);

  const initializeEvaluation = useCallback(async () => {
  if (!enterpriseId || !currentUser) {
    setError('ID de l\'entreprise ou utilisateur manquant');
    setLoading(false);
    return;
  }
  try {
    setLoading(true);
    setError('');
    console.log('üöÄ Initialisation de l\'√©valuation pour entreprise:', enterpriseId);
    let evaluationId: string;
    const navigationState = location.state as { fromInvite?: boolean; inviteId?: string; evaluationId?: string };
    if (navigationState?.fromInvite && navigationState?.evaluationId) {
      console.log('üìß Arriv√©e depuis invitation:', navigationState);
      const response = await api.get(`evaluation-invite/check-evaluation/${navigationState.evaluationId}`);
      evaluationId = response.data.evaluation.id;
      if (response.data.evaluation.statut === 'NOUVEAU') {
        await api.post(`maturity-evaluation/start`, {
          id_entreprise: enterpriseId,
          id_acteur: currentUser?.id_acteur,
          id_evaluation: evaluationId
        });
      }
    } else {
      try {
        const existingResponse = await api.get(`maturity-evaluation/current/${enterpriseId}`);
        evaluationId = existingResponse.data.id_evaluation || `eval_${enterpriseId}_${Date.now()}`;
      } catch {
        const newResponse = await api.post(`maturity-evaluation/start`, {
          id_entreprise: enterpriseId,
          id_acteur: currentUser?.id_acteur
        });
        evaluationId = newResponse.data.id_evaluation;
      }
    }
    const evaluationResponse = await api.get(`evaluation-data/${evaluationId}`);
    const { evaluation, fonctionsGlobales, responses } = evaluationResponse.data;
    // Transform fonctionsGlobales into questionsByFunction format
    const questionsByFunction: QuestionsByFunction = {};
    fonctionsGlobales.forEach((func: any) => {
      questionsByFunction[func.code_fonction] = func.questions.map((q: any) => ({
        id_question: q.id_question,
        fonction: func.code_fonction,
        numero_question: q.numero_question,
        texte_question: q.texte_question,
        description: q.description,
        poids: q.poids,
        type_reponse: q.type_reponse,
        ordre_affichage: q.ordre_question
      }));
    });
    setQuestionsByFunction(questionsByFunction);
    setEvaluationData({
      id_evaluation: evaluation.id_evaluation,
      id_entreprise: evaluation.id_entreprise,
      entreprise_nom: evaluation.nom_entreprise,
      manager_id: evaluation.id_acteur,
      date_debut: evaluation.date_debut,
      statut: evaluation.statut,
      responses,
      scores: {} // Will be calculated client-side
    });
    if (responses && Object.keys(responses).length > 0) {
      setResponses(responses);
      setSnackbarMessage('√âvaluation en cours r√©cup√©r√©e');
      setSnackbarOpen(true);
    }
    console.log('‚úÖ √âvaluation initialis√©e:', evaluationId);
  } catch (error: any) {
    console.error('‚ùå Erreur lors de l\'initialisation:', error);
    setError(error.response?.data?.message || 'Erreur lors du chargement de l\'√©valuation');
  } finally {
    setLoading(false);
  }
}, [enterpriseId, currentUser, location.state]);

  useEffect(() => {
    initializeEvaluation();
  }, [initializeEvaluation]);

  useEffect(() => {
    if (debouncedResponses && Object.keys(debouncedResponses).length > 0 && evaluationData && unsavedChanges) {
      saveResponses(false);
    }
  }, [debouncedResponses]);

  useEffect(() => {
    const handleBeforeUnload = (e: BeforeUnloadEvent) => {
      if (unsavedChanges) {
        e.preventDefault();
        e.returnValue = '';
      }
    };
    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => window.removeEventListener('beforeunload', handleBeforeUnload);
  }, [unsavedChanges]);

  const handleResponseChange = (questionId: string, value: string) => {
    const question = Object.values(questionsByFunction).flat().find(q => q.id_question === questionId);
    if (!question) return;
    const numericValue = parseInt(value);
    const score = (numericValue / 5) * 100;
    const newResponse: Response = {
      id_question: questionId,
      valeur_reponse: value,
      score_question: score
    };
    setResponses(prev => ({
      ...prev,
      [questionId]: newResponse
    }));
    setUnsavedChanges(true);
    setAutoSaveStatus('idle');
  };

  const calculateFunctionScores = (): Record<string, FunctionScore> => {
    const scores: Record<string, FunctionScore> = {};
    Object.keys(functionsConfig).forEach(functionKey => {
      const functionQuestions = questionsByFunction[functionKey] || [];
      const functionResponses = functionQuestions
        .map(q => responses[q.id_question])
        .filter(Boolean);
      const totalQuestions = functionQuestions.length;
      const answeredQuestions = functionResponses.length;
      const progress = totalQuestions > 0 ? (answeredQuestions / totalQuestions) * 100 : 0;
      let averageScore = 0;
      if (functionResponses.length > 0) {
        const totalScore = functionResponses.reduce((sum, response) => sum + response.score_question, 0);
        averageScore = totalScore / functionResponses.length;
      }
      let niveau = 'Non √©valu√©';
      if (averageScore >= 80) niveau = 'Optimis√©';
      else if (averageScore >= 60) niveau = 'G√©r√©';
      else if (averageScore >= 40) niveau = 'Mesur√©';
      else if (averageScore >= 20) niveau = 'D√©fini';
      else if (averageScore > 0) niveau = 'Initial';
      scores[functionKey] = {
        fonction: functionsConfig[functionKey].label,
        score: averageScore,
        niveau,
        progress
      };
    });
    return scores;
  };

  const saveResponses = async (showMessage = true) => {
    if (!evaluationData || Object.keys(responses).length === 0) return;
    try {
      setSaving(true);
      setAutoSaveStatus('saving');
      const responsesArray = Object.values(responses);
      await api.post(`maturity-evaluation/${evaluationData.id_evaluation}/responses-batch`, {
        responses: responsesArray
      });
      setLastSaveTime(new Date());
      setUnsavedChanges(false);
      setAutoSaveStatus('saved');
      if (showMessage) {
        setSnackbarMessage('‚úÖ R√©ponses sauvegard√©es');
        setSnackbarOpen(true);
      }
      setTimeout(() => {
        setAutoSaveStatus('idle');
      }, 3000);
    } catch (error: any) {
      console.error('‚ùå Erreur lors de la sauvegarde:', error);
      setAutoSaveStatus('error');
      setSnackbarMessage('‚ùå Erreur lors de la sauvegarde');
      setSnackbarOpen(true);
    } finally {
      setSaving(false);
    }
  };

 const submitEvaluation = async () => {
  if (!evaluationData) return;
  try {
    setLoading(true);
    await saveResponses(false); // Save any pending responses
    const scores = calculateFunctionScores();
    // Ensure scores format matches backend expectation
    const formattedScores = Object.fromEntries(
      Object.entries(scores).map(([key, value]) => [
        key,
        {
          score: value.score,
          niveau: value.niveau,
          progress: value.progress
        }
      ])
    );
    const response = await api.post(`maturity-evaluation/${evaluationData.id_evaluation}/submit`, {
      scores: formattedScores,
      duree_minutes: Math.round((Date.now() - startTime) / 1000 / 60)
    });
    console.log('‚úÖ √âvaluation soumise:', response.data);
    navigate(`/maturity-analysis/${evaluationData.id_evaluation}`, {
      state: {
        enterpriseId: evaluationData.id_entreprise,
        enterpriseName: evaluationData.entreprise_nom,
        scores: response.data.scores, // Use the backend-returned scores
        fromEvaluation: true
      }
    });
  } catch (error: any) {
    console.error('‚ùå Erreur lors de la soumission:', error);
    setError(error.response?.data?.message || 'Erreur lors de la soumission de l\'√©valuation');
    setSnackbarMessage(error.response?.data?.message || 'Erreur lors de la soumission');
    setSnackbarOpen(true);
  } finally {
    setLoading(false);
  }
};

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    if (unsavedChanges) {
      saveResponses(false);
    }
    setCurrentFunction(newValue);
  };

  const calculateOverallProgress = () => {
    const totalQuestions = Object.values(questionsByFunction).reduce((sum, questions) => sum + questions.length, 0);
    const answeredQuestions = Object.keys(responses).length;
    return totalQuestions > 0 ? (answeredQuestions / totalQuestions) * 100 : 0;
  };

  const isEvaluationComplete = () => {
    const totalQuestions = Object.values(questionsByFunction).reduce((sum, questions) => sum + questions.length, 0);
    return Object.keys(responses).length === totalQuestions;
  };

  const handleConfirmSubmit = () => {
    setShowConfirmDialog(true);
  };

  const handleCloseDialog = () => {
    setShowConfirmDialog(false);
  };

  const handleConfirmDialogSubmit = () => {
    setShowConfirmDialog(false);
    submitEvaluation();
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  if (loading) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Box display="flex" flexDirection="column" alignItems="center" minHeight="60vh" justifyContent="center">
          <CircularProgress size={60} />
          <Typography variant="h6" sx={{ mt: 2 }}>
            Chargement de l'√©valuation...
          </Typography>
        </Box>
      </Container>
    );
  }

  if (error && !evaluationData) {
    return (
      <Container maxWidth="md" sx={{ py: 4 }}>
        <Paper elevation={3} sx={{ p: 4, textAlign: 'center' }}>
          <Error sx={{ fontSize: 64, color: 'error.main', mb: 2 }} />
          <Typography variant="h6" color="error" gutterBottom>
            Erreur
          </Typography>
          <Typography variant="body1" color="text.secondary" sx={{ mb: 3 }}>
            {error}
          </Typography>
          <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center' }}>
            <Button
              variant="outlined"
              onClick={() => navigate('/dashboard')}
              startIcon={<ArrowBack />}
            >
              Retour au tableau de bord
            </Button>
            <Button
              variant="contained"
              onClick={initializeEvaluation}
              startIcon={<Refresh />}
            >
              R√©essayer
            </Button>
          </Box>
        </Paper>
      </Container>
    );
  }

  const functionScores = calculateFunctionScores();
  const overallProgress = calculateOverallProgress();
  const currentFunctionQuestions = questionsByFunction[functionKeys[currentFunction]] || [];

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ p: 3, mb: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
          <Box>
            <Typography variant="h4" gutterBottom>
              √âvaluation de Maturit√© DSIN
            </Typography>
            <Typography variant="h6" color="text.secondary" gutterBottom>
              {evaluationData?.entreprise_nom}
            </Typography>
          </Box>
          <Box sx={{ textAlign: 'right' }}>
            <Chip
              icon={<Timer />}
              label={`${Math.round((Date.now() - startTime) / 1000 / 60)} min`}
              variant="outlined"
              size="small"
            />
            {autoSaveStatus === 'saving' && (
              <Chip
                icon={<CloudUpload />}
                label="Sauvegarde..."
                color="info"
                size="small"
                sx={{ ml: 1 }}
              />
            )}
            {autoSaveStatus === 'saved' && (
              <Chip
                icon={<CloudDone />}
                label="Sauvegard√©"
                color="success"
                size="small"
                sx={{ ml: 1 }}
              />
            )}
            {autoSaveStatus === 'error' && (
              <Chip
                icon={<Error />}
                label="Erreur sauvegarde"
                color="error"
                size="small"
                sx={{ ml: 1 }}
              />
            )}
          </Box>
        </Box>
        <Box sx={{ mb: 2 }}>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
            <Typography variant="body2">
              Progression globale: {Object.keys(responses).length}/{Object.values(questionsByFunction).reduce((sum, q) => sum + q.length, 0)} questions
            </Typography>
            <Typography variant="body2">
              {Math.round(overallProgress)}%
            </Typography>
          </Box>
          <LinearProgress variant="determinate" value={overallProgress} sx={{ height: 8, borderRadius: 4 }} />
        </Box>
        {error && (
          <Alert severity="error" onClose={() => setError('')} sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}
        {lastSaveTime && (
          <Typography variant="caption" color="text.secondary">
            Derni√®re sauvegarde: {lastSaveTime.toLocaleTimeString('fr-FR')}
          </Typography>
        )}
      </Paper>
      <Paper elevation={2} sx={{ mb: 4 }}>
        <Tabs
          value={currentFunction}
          onChange={handleTabChange}
          variant="scrollable"
          scrollButtons="auto"
          sx={{ borderBottom: 1, borderColor: 'divider' }}
        >
          {functionKeys.map((functionKey, index) => {
            const config = functionsConfig[functionKey];
            const score = functionScores[functionKey];
            return (
              <Tab
                key={functionKey}
                icon={config.icon}
                label={
                  <Box>
                    <Typography variant="body2" fontWeight="bold">
                      {config.label}
                    </Typography>
                    {score && (
                      <Typography variant="caption" sx={{ color: config.color }}>
                        {Math.round(score.progress)}% ‚Ä¢ {score.niveau}
                      </Typography>
                    )}
                  </Box>
                }
                sx={{
                  minHeight: 80,
                  '& .MuiTab-iconWrapper': { color: config.color }
                }}
              />
            );
          })}
        </Tabs>
      </Paper>
      <Paper elevation={2} sx={{ p: 4, mb: 4 }}>
        <Box sx={{ mb: 3 }}>
          <Typography variant="h5" gutterBottom sx={{ color: functionsConfig[functionKeys[currentFunction]].color }}>
            {functionsConfig[functionKeys[currentFunction]].label}
          </Typography>
          <Typography variant="body2" color="text.secondary">
            {functionsConfig[functionKeys[currentFunction]].description}
          </Typography>
        </Box>
        <Divider sx={{ mb: 3 }} />
        {currentFunctionQuestions.length === 0 ? (
          <Alert severity="info">
            Aucune question disponible pour cette fonction
          </Alert>
        ) : (
          <Grid container spacing={3}>
            {currentFunctionQuestions.map((question, index) => (
              <Grid item xs={12} key={question.id_question}>
                <Card
                  variant="outlined"
                  sx={{
                    bgcolor: responses[question.id_question] ? 'success.50' : 'background.paper',
                    border: responses[question.id_question] ? 2 : 1,
                    borderColor: responses[question.id_question] ? 'success.main' : 'divider'
                  }}
                >
                  <CardContent>
                    <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 2, mb: 2 }}>
                      <Chip
                        label={question.numero_question}
                        size="small"
                        sx={{
                          bgcolor: functionsConfig[functionKeys[currentFunction]].color,
                          color: 'white'
                        }}
                      />
                      <Box sx={{ flex: 1 }}>
                        <Typography variant="h6" gutterBottom>
                          {question.texte_question}
                        </Typography>
                        {question.description && (
                          <Typography variant="body2" color="text.secondary" gutterBottom>
                            {question.description}
                          </Typography>
                        )}
                      </Box>
                      {responses[question.id_question] && (
                        <CheckCircle color="success" />
                      )}
                    </Box>
                    <FormControl component="fieldset">
                      <FormLabel component="legend">Votre √©valuation:</FormLabel>
                      <RadioGroup
                        row
                        value={responses[question.id_question]?.valeur_reponse || ''}
                        onChange={(e) => handleResponseChange(question.id_question, e.target.value)}
                      >
                        {scaleOptions.map(option => (
                          <FormControlLabel
                            key={option.value}
                            value={option.value}
                            control={<Radio />}
                            label={
                              <Tooltip title={option.description} arrow>
                                <Typography variant="body2">
                                  {option.label}
                                </Typography>
                              </Tooltip>
                            }
                          />
                        ))}
                      </RadioGroup>
                    </FormControl>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        )}
      </Paper>
      <Paper elevation={2} sx={{ p: 4, mb: 4 }}>
        <Typography variant="h6" gutterBottom>
          R√©sum√© des scores par fonction
        </Typography>
        <Grid container spacing={2}>
          {functionKeys.map(functionKey => {
            const config = functionsConfig[functionKey];
            const score = functionScores[functionKey];
            return (
              <Grid item xs={12} sm={6} md={4} key={functionKey}>
                <Card variant="outlined">
                  <CardContent>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                      <Box sx={{ color: config.color }}>
                        {config.icon}
                      </Box>
                      <Typography variant="body2" fontWeight="bold">
                        {config.label}
                      </Typography>
                    </Box>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                      <Typography variant="h6" sx={{ color: config.color }}>
                        {Math.round(score?.score || 0)}%
                      </Typography>
                      <Chip
                        size="small"
                        label={score?.niveau || 'Non √©valu√©'}
                        sx={{ bgcolor: config.color, color: 'white' }}
                      />
                    </Box>
                    <LinearProgress
                      variant="determinate"
                      value={score?.progress || 0}
                      sx={{ height: 6, borderRadius: 3 }}
                    />
                    <Typography variant="caption" color="text.secondary">
                      {Math.round(score?.progress || 0)}% des questions r√©pondues
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            );
          })}
        </Grid>
      </Paper>
      <Paper elevation={2} sx={{ p: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Button
            variant="outlined"
            onClick={() => navigate('/dashboard')}
            startIcon={<ArrowBack />}
            disabled={saving || loading}
          >
            Retour au tableau de bord
          </Button>
          <Box sx={{ display: 'flex', gap: 2 }}>
            <Button
              variant="contained"
              startIcon={<Save />}
              onClick={() => saveResponses(true)}
              disabled={saving || loading || !unsavedChanges}
              sx={{ bgcolor: '#1976d2' }}
            >
              {saving ? 'Sauvegarde...' : 'Sauvegarder'}
            </Button>
            <Button
              variant="contained"
              startIcon={<ArrowForward />}
              onClick={handleConfirmSubmit}
              disabled={saving || loading || !isEvaluationComplete()}
              sx={{ bgcolor: '#09C4B8' }}
            >
              Soumettre l'√©valuation
            </Button>
          </Box>
        </Box>
      </Paper>
      <Dialog open={showConfirmDialog} onClose={handleCloseDialog}>
        <DialogTitle>Confirmer la soumission</DialogTitle>
        <DialogContent>
          <Typography variant="body1">
            √ätes-vous s√ªr de vouloir soumettre votre √©valuation ? Une fois soumise, vous ne pourrez plus modifier vos r√©ponses.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog} color="primary">
            Annuler
          </Button>
          <Button onClick={handleConfirmDialogSubmit} color="primary" variant="contained">
            Soumettre
          </Button>
        </DialogActions>
      </Dialog>
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      />
    </Container>
  );
};

export default MaturityEvaluation;