// src/pages/dashboard/AnalysesInterpretationsEntreprises.tsx
import React, { useState, useEffect } from 'react';
import {
  Container, Grid, Paper, Typography, Box, CircularProgress, Card, CardContent, CardHeader,
  Tabs, Tab, List, ListItem, ListItemText, Divider, Alert, FormControl, InputLabel,
  MenuItem, Select, Chip, Button, Accordion, AccordionSummary, AccordionDetails,
  Avatar, Badge, Tooltip
} from '@mui/material';
import {
  Radar, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, ResponsiveContainer,
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip as RechartsTooltip, Legend,
  PieChart, Pie, Cell
} from 'recharts';
import {
  Business, Assessment, TrendingUp, Security, Computer, Storage, Code, Lightbulb,
  ExpandMore, Refresh, GetApp, Share, Timeline
} from '@mui/icons-material';
import { getNiveauColor } from '../../utils/AnalyseUtils';
import api from '../../services/api';

// Types pour les donn√©es V2 des nouvelles routes
