// src/pages/auth/Login.tsx - Version V2 avec navigation entreprise
import React, { useState } from 'react';
import { useNavigate, useLocation, Link as RouterLink } from 'react-router-dom';
import {
  Box,
  TextField,
  Button,
  Typography,
  Alert,
  CircularProgress,
  Link,
  Divider,
  Card,
  CardContent,
  Chip
} from '@mui/material';
import {
  Login as LoginIcon,
  Business as BusinessIcon,
  Assessment as AssessmentIcon
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';

interface LoginFormData {
  email: string;
  password: string;
}

const Login: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { login } = useAuth();
  
  const [formData, setFormData] = useState<LoginFormData>({
    email: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  // R√©cup√©rer l'URL de redirection depuis l'√©tat de navigation
  const from = location.state?.from?.pathname || '/';

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();
    setLoading(true);
    setError('');

    try {
      await login(formData.email, formData.password);
      
      // Rediriger vers la page demand√©e ou le dashboard
      navigate(from, { replace: true });
    } catch (error: any) {
      console.error('Erreur de connexion:', error);
      setError(
        error.response?.data?.message || 
        'Email ou mot de passe incorrect'
      );
    } finally {
      setLoading(false);
    }
  };

  const isFormValid = formData.email.trim() !== '' && formData.password.trim() !== '';

  return (
    <Box>
      {/* Formulaire de connexion principal */}
      <Box component="form" onSubmit={handleSubmit} noValidate>
        <Typography variant="h4" component="h1" gutterBottom align="center" color="primary">
          Connexion
        </Typography>
        
        <Typography variant="body2" align="center" color="text.secondary" sx={{ mb: 3 }}>
          Acc√©dez √† votre plateforme d'√©valuation de maturit√© DSIN
        </Typography>

        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}

        <TextField
          margin="normal"
          required
          fullWidth
          id="email"
          label="Adresse e-mail"
          name="email"
          autoComplete="email"
          autoFocus
          value={formData.email}
          onChange={handleChange}
          disabled={loading}
          placeholder="votre.email@entreprise.com"
        />

        <TextField
          margin="normal"
          required
          fullWidth
          name="password"
          label="Mot de passe"
          type="password"
          id="password"
          autoComplete="current-password"
          value={formData.password}
          onChange={handleChange}
          disabled={loading}
        />

        <Button
          type="submit"
          fullWidth
          variant="contained"
          disabled={!isFormValid || loading}
          startIcon={loading ? <CircularProgress size={20} /> : <LoginIcon />}
          sx={{ mt: 3, mb: 2, py: 1.5 }}
        >
          {loading ? 'Connexion...' : 'Se connecter'}
        </Button>

        {/* Lien mot de passe oubli√© */}
        <Box sx={{ textAlign: 'center', mb: 3 }}>
          <Link 
            component={RouterLink} 
            to="/auth/forgot-password" 
            variant="body2"
            sx={{ color: 'text.secondary', textDecoration: 'none', '&:hover': { textDecoration: 'underline' } }}
          >
            Mot de passe oubli√© ?
          </Link>
        </Box>
      </Box>

      {/* Divider avec texte */}
      <Divider sx={{ my: 3 }}>
        <Typography variant="body2" color="text.secondary">
          ou
        </Typography>
      </Divider>

      {/* Section d'enregistrement entreprise V2 */}
      <Card variant="outlined" sx={{ backgroundColor: 'rgba(25, 118, 210, 0.02)', border: '1px solid rgba(25, 118, 210, 0.1)' }}>
        <CardContent sx={{ textAlign: 'center', py: 3 }}>
          <Box sx={{ display: 'flex', justifyContent: 'center', mb: 2 }}>
            <BusinessIcon color="primary" sx={{ fontSize: 40 }} />
          </Box>
          
          <Typography variant="h6" gutterBottom color="primary">
            Nouvelle entreprise ?
          </Typography>
          
          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
            Cr√©ez votre compte entreprise et b√©n√©ficiez d'une √©valuation compl√®te de votre maturit√© DSIN
          </Typography>

          {/* Avantages */}
          <Box sx={{ display: 'flex', justifyContent: 'center', gap: 1, mb: 3, flexWrap: 'wrap' }}>
            <Chip 
              icon={<AssessmentIcon />}
              label="√âvaluation gratuite"
              variant="outlined"
              color="primary"
              size="small"
            />
            <Chip 
              label="30 questions expertes"
              variant="outlined"
              color="primary"
              size="small"
            />
            <Chip 
              label="Recommandations personnalis√©es"
              variant="outlined"
              color="primary"
              size="small"
            />
          </Box>

          {/* Bouton d'action principal */}
          <Button
            component={RouterLink}
            to="/auth/enterprise-registration"
            variant="contained"
            size="large"
            startIcon={<BusinessIcon />}
            sx={{ 
              mb: 2,
              minWidth: 240,
              py: 1.2,
              fontSize: '1rem',
              fontWeight: 600
            }}
          >
            Cr√©er un compte entreprise
          </Button>

          {/* Sous-texte promotionnel */}
          <Typography variant="caption" color="text.secondary" sx={{ 
            display: 'block',
            fontStyle: 'italic',
            opacity: 0.8
          }}>
            ‚ú® √âvaluation de maturit√© DSIN ‚Ä¢ 5 fonctions ‚Ä¢ R√©sultats imm√©diats ‚ú®
          </Typography>
        </CardContent>
      </Card>

      {/* Navigation de retour */}
      <Box sx={{ textAlign: 'center', mt: 3 }}>
        <Typography variant="body2" color="text.secondary">
          Vous avez d√©j√† un compte d'entreprise cr√©√© ?{' '}
          <Link 
            component={RouterLink} 
            to="/auth/login"
            sx={{ 
              color: 'primary.main',
              textDecoration: 'none',
              fontWeight: 600,
              '&:hover': { textDecoration: 'underline' }
            }}
          >
            Utilisez le formulaire ci-dessus
          </Link>
        </Typography>
      </Box>

    </Box>
  );
};

export default Login;